import { Component, Element, Event, EventEmitter, Prop, h } from '@stencil/core';
import { InputRoleType, InputType } from '@globals/enums';
import { getPresentValue, removeDoubleSpaces } from '@utils';

@Component({
  tag: 'hdc-input',
  styleUrl: 'hdc-input.scss',
})
export class HdcInput {
  /**
   * Element of hdc input
   */
  @Element() el: HTMLElement;
  /**
   * Prop type of hdc input
   */
  @Prop() type: InputType = InputType.DEFAULT;
  /**
   * Prop roleType of hdc input
   */
  @Prop() roleType: InputRoleType = InputRoleType.TEXT;
  /**
   * Prop placeholder of hdc input
   */
  @Prop({ mutable: false }) placeholder = '';
  /**
   * Prop value of hdc input
   */
  @Prop({ mutable: true }) value: string;
  /**
   * Prop disable of hdc input
   */
  @Prop({ mutable: true }) disable = false;
  /**
   * Prop lock of hdc input
   */
  @Prop({ mutable: true }) lock = false;
  /**
   * Prop lowercase of hdc input
   */
  @Prop({ mutable: true }) lowercase = false;
  /**
   * Prop uppercase of hdc input
   */
  @Prop({ mutable: true }) uppercase = true;
  /**
   * Prop icon left of hdc input
   */
  @Prop({ mutable: true }) iconLeft: string;
  /**
   * Prop color icon left of hdc input
   */
  @Prop({ mutable: true }) colorIconLeft = 'dark-charcoal';
  /**
   * Prop is clicked icon left of hdc input
   */
  @Prop({ mutable: true }) isClickedIconLeft: boolean;
  /**
   * Prop is letters of hdc input
   */
  @Prop() isLetters: boolean;
  /**
   * Prop is alpha numeric of hdc input
   */
  @Prop() isAlphanumeric: boolean;
  /**
   * Prop name of hdc input
   */
  @Prop() name: string;
  /**
   * Event change value of ibk input
   */
  @Event() changeValue: EventEmitter<string>;
  /**
   * Event change blur of ibk input
   */
  @Event() changeBlur: EventEmitter<FocusEvent>;
  /**
   * Event change keydown of ibk input
   */
  @Event() changeKeydown: EventEmitter<KeyboardEvent>;
  /**
   * Event change keyup of ibk input
   */
  @Event() changeKeyup: EventEmitter<KeyboardEvent>;
  /**
   * Event change focus of ibk input
   */
  @Event() changeFocus: EventEmitter<FocusEvent>;
  /**
   * Event change keyenter of ibk input
   */
  @Event() changeEnter: EventEmitter;
  /**
   * Event clicked icon of hdc input
   */
  @Event() clickedIcon: EventEmitter<'LEFT' | 'RIGHT'>;
  /**
   * Regex only letters of hdc input
   */
  private regexLetters = /^[a-zA-ZáéíóúÁÉÍÓÚüÜñÑ\s]+$/;
  /**
   * Regex alphanumeric of hdc input
   */
  private regexAlphanumeric = /^[a-zA-ZáéíóúÁÉÍÓÚüÜñÑ\s\d]+$/;
  render() {
    return (
      <div class={this.getClassWrapper()}>
        {this.iconLeft && (
          <div class={this.getClassInputIconLeft()} onClick={() => this.clickedIcon.emit('LEFT')}>
            <i class={this.getClassIconLeft()}></i>
          </div>
        )}
        <input
          type={this.roleType}
          class="hdc-input__input"
          placeholder={this.placeholder}
          name={this.name}
          id={this.name}
          value={this.value}
          onInput={(ev) => this.onInputText(ev)}
          onBlur={(ev) => this.onBlurInput(ev)}
          onFocus={(ev) => this.onFocusInput(ev)}
          onKeyDown={(ev) => this.onKeydownInput(ev)}
          onKeyUp={(ev) => this.onKeyupInput(ev)}
          disabled={this.disable || this.lock}
        />
      </div>
    );
  }
  /**
   * Determines whether input text on
   * @param input Event
   */
  onInputText(input: InputEvent) {
    const element = input.target as HTMLInputElement;
    let value = element.value;

    if (this.lowercase || this.uppercase) {
      value = removeDoubleSpaces(!this.lowercase ? value.toUpperCase() : value.toLowerCase());
    }

    if (input.data === '. ') {
      value = value.replace(/\. /g, ' ');
    }

    element.value = value;
    this.changeValue.emit(removeDoubleSpaces(element.value));
  }

  /**
   * Determines whether blur input on
   * @param event FocusEvent
   */
  onBlurInput(event: FocusEvent) {
    this.changeBlur.emit(event);
  }

  /**
   * Determines whether focus input on
   * @param event FocusEvent
   */
  onFocusInput(event: FocusEvent) {
    this.changeFocus.emit(event);
  }
  /**
   * Determines whether keydown input on
   * @param event KeyboardEvent
   */
  onKeydownInput(event: KeyboardEvent) {
    if (this.isValidateKey(event)) {
      this.changeKeydown.emit(event);
    } else {
      return;
    }
  }

  /**
   * Determines whether keyup input on
   * @param evemt KeyboardEvent
   */
  onKeyupInput(event: KeyboardEvent) {
    if (this.isValidateKey(event)) {
      this.changeKeyup.emit(event);
    } else {
      return;
    }
  }

  isValidateKey(event: KeyboardEvent) {
    const updateValue = getPresentValue(event);

    if (this.isLetters && !this.regexLetters.test(updateValue)) {
      this.preventDefault(event);
      return false;
    }

    if (this.isAlphanumeric && !this.regexAlphanumeric.test(updateValue)) {
      this.preventDefault(event);
      return false;
    }

    return true;
  }

  preventDefault(event: KeyboardEvent): boolean {
    event.preventDefault();
    return false;
  }
  /**
   * Gets class wrapper
   * @returns { [key: string]: boolean }
   */
  getClassWrapper() {
    return {
      'hdc-input-wrapper': true,
      [`hdc-input-wrapper--${this.type}`]: true,
      [`hdc-input-wrapper--icon-left`]: !!this.iconLeft,
    };
  }

  /**
   * Gets class icon left
   * @returns { [key: string]: boolean }
   */
  getClassIconLeft() {
    return {
      'hdc-icon': true,
      [`hdc-icon-${this.iconLeft}`]: !!this.iconLeft,
      [`color-${this.colorIconLeft}`]: !!this.colorIconLeft,
    };
  }

  /**
   * Gets class input icon left
   * @returns { [key: string]: boolean }
   */
  getClassInputIconLeft() {
    return {
      'hdc-input-icon-left': true,
      'hdc-input-icon-left--pointer': this.isClickedIconLeft,
    };
  }
}
